import * as React from 'react'
import {Layout, NavDrawer, Panel} from 'react-toolbox/lib/layout'
import AppBar from 'react-toolbox/lib/app_bar'

export default class Dashboard extends React.Component<{drawerOpen:false},{
  drawerOpen: boolean
}>{
  constructor(props){
    super(props)

    console.log(this.props)
    console.log(this.state)

    this.toggleDrawer = this.toggleDrawer.bind(this)
  }

  toggleDrawer(){
    const drawerOpen :boolean = !this.state.drawerOpen
    this.setState({drawerOpen})
  }

  render(){
    return (
      <Layout>
        <NavDrawer
          active={this.state.drawerOpen}
          onOverlayClick={this.toggleDrawer}>
          <p>
            Navigation, account switcher, etc. go here.
          </p>
        </NavDrawer>
        <Panel>
          <AppBar
            leftIcon='menu'
            onLeftIconClick={this.toggleDrawer} />
        </Panel>
      </Layout>
    )
  }
}


